import React from 'react';
import styled from 'styled-components';

import PracticeResultTitle from './PracticeResultTitle';
import PracticeStatistics from './PracticeStatistics';
import PracticeQuestionNumbers from './PracticeQuestionNumbers';
import RestartButton from './RestartButton';

const StyledDiv = styled.div`
	display: flex;
	justify-content: center;

	width: 100%;
`;

const PracticeResult = () => {
	return (
		<div>
			<StyledDiv>
				<PracticeResultTitle />
			</StyledDiv>

			<StyledDiv>
				<PracticeStatistics />
			</StyledDiv>

			<PracticeQuestionNumbers />

			<StyledDiv>
				<RestartButton />
			</StyledDiv>
		</div>
	);
};

export default PracticeResult;