const initialRightPlace = {
	place: 1,
	'less': true,
};

const SET_RIGHT_PLACE = 'leftPlace/SET_RIGHT_PLACE';
const CHANGE_RIGHT_LESS = 'leftPlace/CHANGE_RIGHT_LESS';

export const setRightPlace = (place) => {
	return {
		type: SET_RIGHT_PLACE,
		place,
	};
};

export const changeRightLess = () => {
	return {
		type: CHANGE_RIGHT_LESS,
	};
};

export default function rightPlace(state = initialRightPlace, action) {
	switch (action.type) {
		case SET_RIGHT_PLACE:
			return {
				...state,
				place: action.place,
			};
		case CHANGE_RIGHT_LESS:
			return {
				...state,
				less: !state.less,
			};
		default:
			return state;
	}
};