import React from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';

const StyledFooter = styled.footer`
	border-top: 1px solid lightgray;
	padding: 1rem;
	text-align: center;
`;

const StyledFooterText = styled.p`
	margin: 0;
	padding: 0;
`;

const Footer = () => {
	return (
		<StyledFooter>
			<StyledFooterText>&copy; appre.github.io | 사칙연산 연습하기</StyledFooterText>
			<StyledFooterText><Link to="/license">Open Source License</Link></StyledFooterText>
		</StyledFooter>
	);
};

export default Footer;