const sameLength = (leftNumber, rightNumber) => {
	if (leftNumber.length > rightNumber.length) {
		for (let i = 0; i < leftNumber.length - rightNumber.length; ++i) {
			rightNumber = rightNumber + ' ';
		}
	} else if (rightNumber.length > leftNumber.length) {
		for (let i = 0; i < rightNumber.length - leftNumber.length; ++i) {
			leftNumber = ' ' + leftNumber;
		}
	}

	return [leftNumber, rightNumber];
}

export default sameLength;