const initialQuestions = [/*{
	questionNumber: 1,
	leftNumber: 1,
	operator: '+',
	rightNumber: 1,
	quotient: 2,
	rest: 0,
	correct: false,
}*/];

const ADD_QUESTION = 'questions/ADD_QUESTION';
const DELETE_LAST_QUESTION = 'questions/DELETE_LAST_QUESTION';
const SET_CORRECT = 'questions/SET_CORRECT';
const SET_INITIAL_QUESTIONS = 'questions/SET_INITIAL_QUESTIONS';

export const addQuestion = (question) => {
	return {
		type: ADD_QUESTION,
		question,
	};
};

export const deleteLastQuestion = () => {
	return {
		type: DELETE_LAST_QUESTION,
	};
};

export const setCorrect = (questionNumber, correct) => {
	return {
		type: SET_CORRECT,
		questionNumber,
		correct,
	};
};

export const setInitialQuestions = () => {
	return {
		type: SET_INITIAL_QUESTIONS,
	};
};

export default function questions(state = initialQuestions, action) {
	switch (action.type) {
		case ADD_QUESTION:
			return [...state, action.question];
		case DELETE_LAST_QUESTION:
			return state.slice(0, state.length - 1);
		case SET_CORRECT:
			return state.map(question => (
				action.questionNumber === question.questionNumber ? {
					...question, 
					correct: action.correct,
				} : question
			));
		case SET_INITIAL_QUESTIONS:
			return initialQuestions;
		default:
			return state;
	}
}