import React from 'react';
import styled from 'styled-components';
import { useDispatch } from 'react-redux';
import { MediumButton } from '../basic/Button';

import endPractice from '../../utils/practice-paper/endPractice';

const StyledPracticeEndButtonBox = styled.div`
    display: flex;
    justify-content: center;

    width: 100%;

    margin-top: 2rem;
`;

const PracticeEndButton = ({ setOnCorrect, setOnWrong }) => {
    const dispatch = useDispatch();

    /*const onClick = () => {
        setOnCorrect(false);
        setOnWrong(false);
        
        dispatch(changeToResultProgress());
    }*/

    const onClick = () => endPractice(setOnCorrect, setOnWrong, dispatch);

    return (
        <StyledPracticeEndButtonBox>
            <MediumButton color="Black" onClick={onClick}>연습 종료</MediumButton>
        </StyledPracticeEndButtonBox>
    );
};

export default PracticeEndButton;