import React from 'react';
import styled from 'styled-components';
import { useSelector, useDispatch } from 'react-redux';
import { changeToResultProgress } from '../../modules/practiceProgress';
import { SOLVING_PROGRESS } from '../../modules/solvingProgress';
import { deleteLastQuestion } from '../../modules/questions';

import { SmallButton } from '../basic/Button';

const StyledDiv = styled.div`
    display: flex;
    flex-direction: row-reverse;

    width: 100%;
`;

const PracticeStopButton = () => {
    const { solvingProgress } = useSelector(state => ({
        solvingProgress: state.practiceProgress.solvingProgress,
    }));

    const dispatch = useDispatch();

    const onClick = () => {
        const wantStop = window.confirm('정말로 연습을 그만두시겠습니까?');

        if (wantStop) {
            if (solvingProgress === SOLVING_PROGRESS) {
                dispatch(deleteLastQuestion());
            }

            dispatch(changeToResultProgress());
        }
    }

    return (
        <StyledDiv>
            <SmallButton color="Black" onClick={onClick}>연습 그만하기</SmallButton>
        </StyledDiv>
    );
};

export default PracticeStopButton;