import React from 'react';
import { MdCheckBox, MdCheckBoxOutlineBlank } from 'react-icons/md';
import styled from 'styled-components';

const StyledCheckbox = styled.div`
	display: inline-flex;
	align-items: center;
`;

const StyledLabel = styled.label`
	cursor: pointer;
`;

const StyledInputCheckbox = styled.input`
	width: 0;
	height: 0;
	position: absolute;
	opacity: 0;
`;

const StyledIcon = styled.div`
	display: flex;
	align-items: center;
	font-size: 1.5rem;
	margin-left: 0.25rem;
	color: #adb5bd;
`;

const Checkbox = ({checked, children, onChange, name, ...rest}) => {
	return (
		<StyledCheckbox>
			<StyledLabel>
				<StyledInputCheckbox type="checkbox" checked={checked} onChange={onChange} name={name} {...rest} />
				<StyledIcon>{checked ? <MdCheckBox style={{ color: '#339af0' }} /> : <MdCheckBoxOutlineBlank />}</StyledIcon>
			</StyledLabel>
			<span>{children}</span>
		</StyledCheckbox>
	);
};

export default Checkbox;