const initialQuestionCount = 10;

const SET_QUESTION_COUNT = 'questionCount/SET_QUESTION_COUNT';

export const setQuestionCount = (count) => {
	return {
		type: SET_QUESTION_COUNT,
		count,
	};
};

export default function questionCount(state = initialQuestionCount, action) {
	switch (action.type) {
		case SET_QUESTION_COUNT:
			return action.count;
		default:
			return state;
	}
};
