import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { setOperator } from '../../modules/operators';
import OperatorsPresenter from './OperatorsPresenter';

const Operators = () => {
	const { operators } = useSelector(state => ({
		operators: state.operators,
	}));

	const dispatch = useDispatch();
	const dispatchOperator = operator => {
		dispatch(setOperator(operator));
	};

	return (
		<OperatorsPresenter
			operators={operators}
			dispatchOperator={dispatchOperator}
		/>
	);
};

export default Operators;