import React from 'react';
import styled from 'styled-components';
import { useSelector, useDispatch } from 'react-redux';

import { MediumButton } from '../basic/Button';

import toNextQuestion from '../../utils/practice-paper/toNextQuestion';

const StyledNextQuestionButtonBox = styled.div`
	display: flex;
	justify-content: center;

	width: 100%;

	margin-top: 2rem;
`;

const NextQuestionButton = ({ setQuotient, setRest, setOnCorrect, setOnWrong }) => {
	const { questions, leftPlace, rightPlace, operators } = useSelector(state => ({
        questions: state.questions,
        leftPlace: state.leftPlace,
        rightPlace: state.rightPlace,
        operators: state.operators,
    }));

    const dispatch = useDispatch();

    const onClick = () => toNextQuestion(questions, leftPlace, operators, rightPlace, setOnCorrect, setOnWrong, setQuotient, setRest, dispatch);

	return (
		<StyledNextQuestionButtonBox>
			<MediumButton color="Black" onClick={onClick}>다음 문제</MediumButton>
		</StyledNextQuestionButtonBox>
	);
};

export default NextQuestionButton;