import React from 'react';
import styled from 'styled-components';
import sameLength from '../../utils/sameLength';

const StlyedQuestion = styled.div`
	display: flex;
	justify-content: center;

	width: 100%;

	margin-top: 2rem;
	margin-bottom: 2rem;
`;

const StyledQuestionText = styled.span`
	white-space: pre-wrap;

	font-size: 2rem;

	&:before {
		content: '${props => props.leftNumber}';
		font-size: 2rem;
	}

	&:after {
		content: '${props => props.rightNumber}';
		font-size: 2rem;
	}
`;

const Question = ({ leftNumber, operator, rightNumber }) => {
    if (leftNumber === undefined || operator === undefined || rightNumber === undefined) {
    	return;
    }

	const [displayLeftNumber, displayRightNumber] = sameLength(leftNumber, rightNumber);
	const displayOperator = operator === '/' ? '÷' : operator === '*' ? '×' : operator;

	return (
		<StlyedQuestion>
			<StyledQuestionText>{`${displayLeftNumber}${displayOperator}${displayRightNumber}`}</StyledQuestionText>
		</StlyedQuestion>
	);
};

export default Question;