const initialOperators = {
	'+': true,
	'-': false,
	'*': false,
	'/': false,
};

const SET_OPERATOR = 'operators/SET_OPERATOR';

export const setOperator = (operator) => {
	return {
		type: SET_OPERATOR,
		operator,
	};
};

export default function operators(state = initialOperators, action) {
	switch (action.type) {
		case SET_OPERATOR:
			return {
				...state,
				[action.operator]: !state[action.operator],
			};
		default:
			return state;
	}
};