import React from 'react';
import styled from 'styled-components';

const StyledButton = styled.button`
	display: inline-flex;
	justify-content: center;
	align-items: center;

	color: ${props => props.color};
	height: ${props => props.height}rem;
	background: none;
	border: 1px solid ${props => props.color};
	font-size: ${props => props.fontSize}rem;
	border-radius: 4px;
	font-weight: bold;

	&:hover {
		background: ${props => props.color};
		opacity: 0.8;
		color: white;
	}

	padding-left: ${props => props.padding}rem;
	padding-right: ${props => props.padding}rem;

	cursor: pointer;
	outline: none;
`;

const Button = ({children, color, height, fontSize, padding, onClick}) => {
	return (
		<StyledButton color={color} height={height} fontSize={fontSize} padding={padding} onClick={onClick}>{children}</StyledButton>
	);
};

export const MediumButton = ({children, color, onClick}) => {
	return (
		<Button color={color} height="2.25" fontSize="1" padding="1" onClick={onClick}>{children}</Button>
	);
};

export const SmallButton = ({children, color, onClick}) => {
	return (
		<Button color={color} height="1.7" fontSize="0.75" padding="0.7" onClick={onClick}>{children}</Button>
	);
};

export default Button;