import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { setLeftPlace, changeLeftLess } from '../../modules/leftPlace';
import LeftPlacePresenter from './LeftPlacePresenter';

const LeftPlace = () => {
	const { leftPlace } = useSelector(state => ({
		leftPlace: state.leftPlace,
	}));

	const dispatch = useDispatch();
	const setPlace = place => {
		dispatch(setLeftPlace(place));
	};
	const changeLess = () => {
		dispatch(changeLeftLess());
	};

	return (
		<LeftPlacePresenter
			leftPlace={leftPlace}
			setLeftPlace={setPlace}
			onLessChange={changeLess}
		/>
	);
};

export default LeftPlace;