import React from 'react';
import styled from 'styled-components';
import { useSelector, useDispatch } from 'react-redux';
import { SOLVING_PROGRESS, SOLVED_PROGRESS, END_PROGRESS } from '../../modules/solvingProgress';

import onSubmit from '../../utils/practice-paper/onSubmit';
import toNextQuestion from '../../utils/practice-paper/toNextQuestion';
import endPractice from '../../utils/practice-paper/endPractice';

const StyledAnswerBox = styled.div`
    display: flex;
    justify-content: center;

    width: 100%;
`;

const StlyedAnswer = styled.input`
    height: 1.7rem;
    font-size: 1.7rem;
    width: ${props => props.width}rem;

    & + & {
        margin-left: 1rem;
    }
`;

const Answer = ({quotient, setQuotient, rest, setRest, setOnCorrect, setOnWrong}) => {
    const { questions, questionCount, solvingProgress, leftPlace, operators, rightPlace } = useSelector(state => ({
        questions: state.questions,
        questionCount: state.questionCount,
        solvingProgress: state.practiceProgress.solvingProgress,
        leftPlace: state.leftPlace,
        operators: state.operators,
        rightPlace: state.rightPlace,
    }));

    const operator = questions[questions.length - 1]?.operator;

    const onChangeQuotient = e => {
        if (e.target.value === '' || e.target.value === '-' || String(parseInt(e.target.value, 10)) === e.target.value) {
            setQuotient(e.target.value);
        }
    };

    const onChangeRest = e => {
        if (e.target.value === '' || e.target.value === '-' || String(parseInt(e.target.value, 10)) === e.target.value) {
            setRest(e.target.value);
        }
    };

    const dispatch = useDispatch();

    const onEnter = e => {
        if (e.key === 'Enter') {
            if (solvingProgress === SOLVING_PROGRESS) {
                onSubmit(questionCount, questions, quotient, rest, dispatch, setOnCorrect, setOnWrong);
            } else if (solvingProgress === SOLVED_PROGRESS) {
                toNextQuestion(questions, leftPlace, operators, rightPlace, setOnCorrect, setOnWrong, setQuotient, setRest, dispatch);
            } else if (solvingProgress === END_PROGRESS) {
                endPractice(setOnCorrect, setOnWrong, dispatch);
            }
        }
    };

    const readOnly = solvingProgress === SOLVED_PROGRESS;

    return (
        <StyledAnswerBox>
            {
                operator !== '/' ? 
                <StlyedAnswer 
                    width="10" 
                    value={quotient} 
                    onChange={onChangeQuotient} 
                    placeholder="정답" 
                    onKeyPress={onEnter} 
                    readOnly={readOnly} 
                /> : 
                <span>
                    <StlyedAnswer width="5" value={quotient} onChange={onChangeQuotient} placeholder="몫" onKeyPress={onEnter} readOnly={readOnly} />
                    <StlyedAnswer width="5" value={rest} onChange={onChangeRest} placeholder="나머지" onKeyPress={onEnter} readOnly={readOnly} />
                </span>
            }
        </StyledAnswerBox>
    );
};

export default Answer;