import React from 'react';
import styled from 'styled-components';
import { useSelector } from 'react-redux';

import getCorrectCount from '../../utils/getCorrectCount';

const StyledDiv = styled.div`
    white-space: pre-wrap;

    margin-top: 1rem;
    margin-bottom: 1rem;
`;

const PracticeStatistics = () => {
    const { questions } = useSelector(state => ({
        questions: state.questions,
    }));

    const questionCount = questions.length;
    const correctCount = getCorrectCount(questions);

    const wrongCount = questionCount - correctCount;
    const wrongRate = questionCount === 0 ? '-' : Math.ceil(((wrongCount / questionCount) * 10000) / 100);

    return (
        <StyledDiv>
            {`푼 문제: ${questionCount}  정답: ${correctCount}  오답: ${wrongCount}  오답률: ${wrongRate}%`}
        </StyledDiv>
    );
};

export default PracticeStatistics;