import React from 'react';
import styled from 'styled-components';
import { useDispatch } from 'react-redux';

import { MediumButton } from '../basic/Button';

import { setInitialQuestions } from '../../modules/questions';
import { changeToSettingProgress } from '../../modules/practiceProgress';

const StyledDiv = styled.div`
    margin-top: 1rem;
    margin-bottom: 1rem;
`;

const RestartButton = () => {
    const dispatch = useDispatch();

    const onClick = () => {
        dispatch(setInitialQuestions());
        dispatch(changeToSettingProgress());
    }

    return (
        <StyledDiv>
            <MediumButton color="Black" onClick={onClick}>다시 연습하기</MediumButton>
        </StyledDiv>
    );
};

export default RestartButton;