import React from 'react';
import styled from 'styled-components';
import correctQuestionCount from '../../utils/correctQuestionCount';

const StyledDiv = styled.div`
    margin-top: 1rem;
    margin-bottom: 1rem;
`;

const StyledInput = styled.input`
    &::-webkit-outer-spin-button {
        -webkit-appearance: none;
        margin: 0;
    }

    &::-webkit-inner-spin-button {
        -webkit-appearance: none;
        margin: 0;
    }

    -moz-appearance: textfield;
    width: 5rem;
`;

const QuestionCountPresenter = ({ questionCount, onSetQuestionCount }) => {
    const onChange = e => {
        onSetQuestionCount(correctQuestionCount(e.target.value, questionCount));
    }

    return (
        <StyledDiv>
            <span>문제 수:&nbsp;</span>
            <span>
                <StyledInput type="number" placeholder="1~100" value={questionCount} onChange={onChange} />
            </span>
        </StyledDiv>
    );
};

export default QuestionCountPresenter;