import React from 'react';
import styled from 'styled-components';
import Checkbox from '../basic/Checkbox';

const StyledDiv = styled.div`
	display: flex;
	margin-top: 1rem;
	margin-bottom: 1rem;
`;

const StyledCheckboxes = styled.div`
	display: flex;
	align-items: center;
`;

const OperatorsPresenter = ({ operators, dispatchOperator }) => {
	const onChange = e => {
		dispatchOperator(e.target.name);
	};

	return (
		<StyledDiv>
			<StyledCheckboxes>
                <span>연산자:&nbsp;</span>
                <Checkbox name='+' checked={operators['+']} onChange={onChange}>+</Checkbox>
                <Checkbox name='-' checked={operators['-']} onChange={onChange}>-</Checkbox>
                <Checkbox name='*' checked={operators['*']} onChange={onChange}>&times;</Checkbox>
                <Checkbox name='/'checked={operators['/']} onChange={onChange}>÷</Checkbox>
            </StyledCheckboxes>
		</StyledDiv>
	)
};

export default OperatorsPresenter;