import { changeToPracticeProgress } from '../../../modules/practiceProgress';
import { setCorrect } from '../../../modules/questions';
import { SOLVED_PROGRESS, END_PROGRESS } from '../../../modules/solvingProgress';

const onSubmitAnswer = function(questionCount, questions, quotient, rest, dispatch, setOnCorrect, setOnWrong) {
    if (questions[questions.length - 1].operator === '/') {
        if (quotient === '' && rest === '') {
            window.alert("정답을 입력하세요.");
            return;
        }
    } else {
        if (quotient === '') {
            window.alert("정답을 입력하세요.");
            return;
        }
    }
    
    let correct;
    if (questions[questions.length - 1].operator === '/') {
        correct = +questions[questions.length - 1].quotient === +quotient && +questions[questions.length - 1].rest === +rest;
    } else {
        correct = +questions[questions.length - 1].quotient === +quotient
    }

    dispatch(setCorrect(questions[questions.length - 1].questionNumber, correct));

    if (correct) {
        setOnCorrect(true);
        setOnWrong(false);
    } else {
        setOnCorrect(false);
        setOnWrong(true);
    }

    if (+questions[questions.length - 1].questionNumber === +questionCount) {
        dispatch(changeToPracticeProgress(END_PROGRESS));
        return;
    }

    dispatch(changeToPracticeProgress(SOLVED_PROGRESS));
}

export default onSubmitAnswer;