import React from 'react';
import styled from 'styled-components';

const StyledDiv = styled.div`
    margin-top: 1rem;
    margin-bottom: 1rem;
`;

const StyledTitle = styled.h2`
    margin: 0;
    padding: 0;

    font-size: 1rem;
`;

const PracticeResultTitle = () => {
    return (
        <StyledDiv>
            <StyledTitle>연습 결과</StyledTitle>
        </StyledDiv>
    )
};

export default PracticeResultTitle;