import React from 'react';
import styled from 'styled-components';
import Checkbox from '../basic/Checkbox';

const StyledDiv = styled.div`
	display: flex;
    margin-top: 1rem;
    margin-bottom: 1rem;
`;

const LeftPlacePresenter = ({ leftPlace, setLeftPlace, onLessChange }) => {
	const onSetLeftPlace = e => {
		setLeftPlace(e.target.value);
	}

	return (
		<StyledDiv>
			<span>왼쪽 자릿수:&nbsp;</span>
			<span>
				<select value={leftPlace.place} onChange={onSetLeftPlace}>
	                <option value="1">1자리</option>
	                <option value="2">2자리</option>
	                <option value="3">3자리</option>
	                <option value="4">4자리</option>
	            </select>
	        </span>
	        <span><Checkbox checked={leftPlace.less} onChange={onLessChange}>이하 포함</Checkbox></span>
		</StyledDiv>
	);
};

export default LeftPlacePresenter;