import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { setQuestionCount } from '../../modules/questionCount';
import QuestionCountPresenter from './QuestionCountPresenter';

const QuestionCount = () => {
	const { count } = useSelector(state => ({
		count: state.questionCount,
	}));

	const dispatch = useDispatch();
	const onSetQuestionCount = newCount => {
		dispatch(setQuestionCount(newCount));
	};

	return (
		<QuestionCountPresenter
			questionCount={count}
			onSetQuestionCount={onSetQuestionCount}
		/>
	);
};

export default QuestionCount;