import { addQuestion } from '../../modules/questions';
import newQuestion from '../newQuestion';
import { changeToPracticeProgress } from '../../modules/practiceProgress';
import { SOLVING_PROGRESS } from '../../modules/solvingProgress';

const toNextQuestion = function(questions, leftPlace, operators, rightPlace, setOnCorrect, setOnWrong, setQuotient, setRest, dispatch) {
    setOnCorrect(false);
    setOnWrong(false);

    setQuotient('');
    setRest('');

    dispatch(addQuestion(newQuestion(questions[questions.length - 1].questionNumber + 1, leftPlace, operators, rightPlace)));
    dispatch(changeToPracticeProgress(SOLVING_PROGRESS));
};

export default toNextQuestion;