import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { setRightPlace, changeRightLess } from '../../modules/rightPlace';
import RightPlacePresenter from './RightPlacePresenter';

const RightPlace = () => {
	const { rightPlace } = useSelector(state => ({
		rightPlace: state.rightPlace,
	}));

	const dispatch = useDispatch();
	const setPlace = place => {
		dispatch(setRightPlace(place));
	};
	const changeLess = () => {
		dispatch(changeRightLess());
	};

	return (
		<RightPlacePresenter
			rightPlace={rightPlace}
			setRightPlace={setPlace}
			onLessChange={changeLess}
		/>
	);
};

export default RightPlace;