const initialLeftPlace = {
	place: 1,
	'less': true,
};

const SET_LEFT_PLACE = 'leftPlace/SET_LEFT_PLACE';
const CHANGE_LEFT_LESS = 'leftPlace/CHANGE_LEFT_LESS';

export const setLeftPlace = (place) => {
	return {
		type: SET_LEFT_PLACE,
		place,
	};
};

export const changeLeftLess = () => {
	return {
		type: CHANGE_LEFT_LESS,
	};
};

export default function leftPlace(state = initialLeftPlace, action) {
	switch (action.type) {
		case SET_LEFT_PLACE:
			return {
				...state,
				place: action.place,
			};
		case CHANGE_LEFT_LESS:
			return {
				...state,
				less: !state.less,
			};
		default:
			return state;
	}
};