import React from 'react';
import styled from 'styled-components';
import { useSelector, useDispatch } from 'react-redux';
import { addQuestion } from '../../modules/questions';
import newQuestion from '../../utils/newQuestion';
import { changeToPracticeProgress } from '../../modules/practiceProgress';
import { SOLVING_PROGRESS } from '../../modules/solvingProgress';
import { MediumButton } from '../basic/Button';

const StyledDiv = styled.div`
    display: flex;
    margin-top: 1rem;
    margin-bottom: 1rem;
`;

const PracticeStartButton = () => {
    const { questionCount, operators, leftPlace, rightPlace } = useSelector(state => ({
        questionCount: state.questionCount,
        operators: state.operators,
        leftPlace: state.leftPlace, 
        rightPlace: state.rightPlace, 
    }));

    const dispatch = useDispatch();

    const onClick = () => {
        if (questionCount === '') {
            alert("문제 수를 입력하세요.");
            return;
        }

        if (!operators['+'] && !operators['-'] && !operators['*'] && !operators['/']) {
            alert("연산자를 설정하세요.");
            return;
        }

        dispatch(addQuestion(newQuestion(1, leftPlace, operators, rightPlace)));
        dispatch(changeToPracticeProgress(SOLVING_PROGRESS));
    }

    return (
        <StyledDiv>
            <MediumButton color="Black" onClick={onClick}>연습 시작</MediumButton>
        </StyledDiv>
    );
};

export default PracticeStartButton;