import { SOLVING_PROGRESS } from './solvingProgress';

export const SETTING_PROGRESS = {
    name: "practiceProgress/SETTING_PROGRESS",
};

export const PRACTICE_PROGRESS = {
    name: "practiceProgress/PRACTICE_PROGRESS",
    solvingProgress: SOLVING_PROGRESS,
};

export const RESULT_PROGRESS = {
    name: "practiceProgress/RESULT_PROGRESS",
};

export const REVIEW_PROGRESS = {
    name: "practiceProgress/REVIEW_PROGRESS",
};

const getSettingProgress = () => SETTING_PROGRESS;

const getPracticeProgress = solvingProgress => ({
    ...PRACTICE_PROGRESS,
    solvingProgress,
});

const getResultProgress = () => RESULT_PROGRESS;

const getReviewProgress = (questionNumber, solvingProgress) => ({
    ...REVIEW_PROGRESS,
    questionNumber,
    solvingProgress,
});



const CHANGE_PRACTICE_PROGRESS = "practiceProgress/CHANGE_PRACTICE_PROGRESS";

const changePracticeProgress = practiceProgress => ({
    type: CHANGE_PRACTICE_PROGRESS,
    practiceProgress,
});

export const changeToSettingProgress = () => changePracticeProgress(getSettingProgress());

export const changeToPracticeProgress = solvingProgress => changePracticeProgress(getPracticeProgress(solvingProgress));

export const changeToResultProgress = () => changePracticeProgress(getResultProgress());

export const changeToReviewProgress = (questionNumber, solvingProgress) => (
    changePracticeProgress(getReviewProgress(questionNumber, solvingProgress))
);

export default function practiceProgress(state = SETTING_PROGRESS, action) {
    switch (action.type) {
        case CHANGE_PRACTICE_PROGRESS:
            return action.practiceProgress;
        default:
            return state;
    }
}