import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import styled from 'styled-components';

import { MediumButton } from '../basic/Button';

import onSubmit from '../../utils/practice-paper/onSubmit';

const StyledSubmitButtonBox = styled.div`
    display: flex;
    justify-content: center;

    width: 100%;

    margin-top: 2rem;
`;

const SubmitButton = ({ quotient, rest, setOnCorrect, setOnWrong }) => {
    const { questions, questionCount } = useSelector(state => ({
        questions: state.questions,
        questionCount: state.questionCount,
    }));

    const dispatch = useDispatch();

    const onClick = () => onSubmit(questionCount, questions, quotient, rest, dispatch, setOnCorrect, setOnWrong);

    return (
        <StyledSubmitButtonBox>
            <MediumButton color="Black" onClick={onClick}>정답 제출</MediumButton>
        </StyledSubmitButtonBox>
    );
};

export default SubmitButton;