import React from 'react';
import styled from 'styled-components';

const StyledDiv = styled.div`
	display: flex;
	margin-top: 1rem;
	margin-bottom: 1rem;
`;

const StyledTitle = styled.h2`
	margin: 0;
	padding: 0;

	font-size: 1rem;
`;

const PracticeSettingsTitle = () => {
	return (
		<StyledDiv>
			<StyledTitle>연습 설정</StyledTitle>
		</StyledDiv>
	);
};

export default PracticeSettingsTitle;