const toOperatorsArray = operatorsSetting => {
    const operators = [];
    for (let operator in operatorsSetting) {
        if (operatorsSetting[operator]) {
            operators.push(operator);
        }
    }

    return operators;
};

const createNumber = (placeInfo, noZero) => {
    let place = placeInfo.place;
    let number;

    if (placeInfo['less'] === true) {
        number = createNumber({place: Math.floor((Math.random() * place) + 1), 'less': false,}, noZero);
    } else {
        if (placeInfo['place'] === 1) {
            number = Math.floor(Math.random() * 10);
        } else {
            number = Math.floor((Math.random() * (10 ** place - 10 ** (place - 1))) + 10 ** (place - 1));
        }
    }

    if (noZero && number === 0) {
        number = createNumber(placeInfo, noZero);
    }

    return number;
};

const createOperator = (operatorsSetting) => {
    let operators = toOperatorsArray(operatorsSetting);
    return operators[Math.floor(Math.random() * operators.length)];
};

const newQuestion = (questionNumber, leftPlaceSetting, operatorsSetting, rightPlaceSetting) => {
    const operator = createOperator(operatorsSetting);

    let rightNumber;
    if (operator === '/') {
        rightNumber = createNumber(rightPlaceSetting, true);
    } else {
        rightNumber = createNumber(rightPlaceSetting, false);
    }
    let leftNumber = createNumber(leftPlaceSetting, false);

    return {
        questionNumber,
        leftNumber,
        operator,
        rightNumber,
        // eslint-disable-next-line
        quotient: window.eval(`Math.floor(${leftNumber}${operator}${rightNumber})`),
        // eslint-disable-next-line
        rest: window.eval(`${leftNumber} % ${rightNumber}`),
        correct: false,
    };
};

export default newQuestion;