import React, { useState } from 'react';
import styled from 'styled-components';
import { useSelector } from 'react-redux';

import { SOLVING_PROGRESS, SOLVED_PROGRESS, END_PROGRESS } from '../../modules/solvingProgress';

import PracticeStopButton from './PracticeStopButton';
import QuestionNumber from './QuestionNumber';
import Question from './Question';
import Answer from './Answer';
import SubmitButton from './SubmitButton';
import NextQuestionButton from './NextQuestionButton';
import PracticeEndButton from './PracticeEndButton';
import Correct from './Correct';
import Wrong from './Wrong';

const StyledDiv = styled.div`
    position: relative;
`;

const PracticePaper = () => {
    const [quotient, setQuotient] = useState('');
    const [rest, setRest] = useState('');
    const [onCorrect, setOnCorrect] = useState(false);
    const [onWrong, setOnWrong] = useState(false);

    const { solvingProgress, questions } = useSelector(state => ({
        solvingProgress: state.practiceProgress.solvingProgress,
        questions: state.questions,
    }));

    return (
        <StyledDiv>
            <Correct on={onCorrect} />
            <Wrong on={onWrong} />

            <PracticeStopButton />

            <QuestionNumber questionNumber={questions[questions.length - 1]?.questionNumber} />

            <Question 
                leftNumber={questions[questions.length - 1]?.leftNumber}
                operator={questions[questions.length - 1]?.operator}
                rightNumber={questions[questions.length - 1]?.rightNumber}
            />

            <Answer 
                quotient={quotient} 
                setQuotient={setQuotient} 
                rest={rest} 
                setRest={setRest} 
                setOnCorrect={setOnCorrect} 
                setOnWrong={setOnWrong} 
            />

            {solvingProgress === SOLVING_PROGRESS ? 
                <SubmitButton 
                    quotient={quotient} 
                    rest={rest} 
                    setOnCorrect={setOnCorrect} 
                    setOnWrong={setOnWrong} 
                /> : null}

            {solvingProgress === SOLVED_PROGRESS ? 
                <NextQuestionButton setQuotient={setQuotient} setRest={setRest} setOnCorrect={setOnCorrect} setOnWrong={setOnWrong} /> : null}
            {solvingProgress === END_PROGRESS ? 
                <PracticeEndButton setOnCorrect={setOnCorrect} setOnWrong={setOnWrong} /> : null}
        </StyledDiv>
    );
};

export default PracticePaper;